import styled from "styled-components";
import PropTypes from "prop-types";

function FlexBox({children, type = "rows"}) {
    if (type === `columns`) return <FlexBoxWrapperColumns>{children}</FlexBoxWrapperColumns>
    if (type === `rows`) return <FlexBoxWrapperRows>{children}</FlexBoxWrapperRows>
    return <FlexBoxWrapper>{children}</FlexBoxWrapper>
}

FlexBox.propTypes = {
    type: PropTypes.oneOf(["rows", "columns"])
}
export default FlexBox;
const FlexBoxWrapper = styled.div`
  display: flex;
  position: relative;
  width: 100%;
`;
const FlexBoxWrapperRows = styled(FlexBoxWrapper)`
  flex-direction: row;
  width: auto;
  flex-wrap: wrap;
`;
const FlexBoxWrapperColumns = styled(FlexBoxWrapper)`
  flex-direction: column;
  width: 100%;
`;